import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { toggleSideNav } from '../../../actions/actions';

import './BackDrop.css'

class BackDrop extends Component {
    render() {
        if(this.props.sideNavOpen){
            return (
                <div className="back-drop-container" style={{ visibility: 'visible' }} onClick={() => this.props.toggleSideNav()}/>
            )
        } else {
            return (
                <div className="back-drop-container" style={{ visibility: 'hidden' }}/>
            )
        }
        
    }

}

function mapStateToProps(state) {
    return {
        sideNavOpen: state.uiState.sideNavOpen
    }
}

function matchDispatchToProps(dispatch){
    return bindActionCreators({toggleSideNav: toggleSideNav}, dispatch);
}

export default connect(mapStateToProps, matchDispatchToProps)(BackDrop);