import React, { Component } from 'react';
import { connect } from 'react-redux';
import ReceiptSock from './../shopping_cart_page/page_cart_record/PageCartRecord';

import './ShoppingCartPage.css';

class ShoppingCartPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            //online: false
            selectedMethod: ""
        }
    }

    render() {
        //window.addEventListener('offline', function(e) { });
        //window.addEventListener('online', function(e) { });

        var online = navigator.onLine;

        return (
            <div className="shopping-page-cart-container">
                <div className="shopping-page-cart-receipt">
                    {this.props.cart.map(sock => <ReceiptSock key={sock.id} {...sock}>{sock.name}</ReceiptSock>)}
                    {this.props.cart.length > 0 ?
                        <div className="shopping-page-cart-total-price"> Total price: € </div>
                        :
                        <div>Shopping cart empty :(</div>
                    }
                </div>
                <div className="shopping-page-cart-payment">
                    <div className="shopping-page-cart-payment-title">Check out</div>
                    <div className="payment-methods">
                        <div id="ideal" className={this.state.selectedMethod === "ideal" ? "payment-method selected-payment-method" : "payment-method"} onClick={() => { this.selectPaymentMethod("ideal") }}>
                            iDeal
                            <div className="payment-method-overlay" style={online ? { visibility: 'collapse' } : { visibility: 'show' }}></div>
                        </div>
                        <div id="afterpay" className={this.state.selectedMethod === "afterpay" ? "payment-method selected-payment-method" : "payment-method"} onClick={() => { this.selectPaymentMethod("afterpay") }}>
                            AfterPay
                        </div>
                    </div>
                    <div className={this.state.selectedMethod !== "" ? "place-order-button place-order-button-selected" : "place-order-button"} onClick={() => { this.placeOrder() }}>Place order</div>
                </div>
            </div>
        )
    }

    selectPaymentMethod(method) {
        switch (method) {
            case "ideal":
                if (navigator.onLine) {
                    console.log("ideal mag want online");
                    this.setState({ selectedMethod: method });
                } else {
                    console.log("ideal mag toch niet want offline");
                }
                break;
            case "afterpay":
                console.log("afterpay");
                this.setState({ selectedMethod: method });
                break;
            default:
                console.log("default");
                this.setState({ selectedMethod: "" });
                break;
        }
    }

    placeOrder(){
        if(this.state.selectedMethod !== ""){
            console.log("order placed");
        } else {
            console.log("order niet placed");
        }
    }
}

function mapStateToProps(state) {
    return { cart: state.cartState }
}

export default connect(mapStateToProps)(ShoppingCartPage);